import React from "react";

const Image = React.forwardRef((props, ref) => {
  const { keepAspect, caption, alt, src, srcset, width, height, sizes } = props;
  console.log(props);
  var style = {};

  var orientation = "landscape";

  if (height >= width) {
    orientation = "portrait";
  }

  // calculate aspect ratio
  if (keepAspect) {
    style = {
      "--aspect-ratio": `${(height / width) * 100}%`
    };
  }

  return (
    <figure className={`${keepAspect ? "is-aspect" : ""} is-${orientation}`} style={style} ref={ref}>
      <img src={src} srcSet={sizes ? srcset : ""} sizes={sizes ? sizes : ""} alt={alt}></img>
      {caption && (
        <figcaption>
          <span>{caption}</span>
        </figcaption>
      )}
    </figure>
  );
});

export default Image;
