import { useRef, useEffect, useCallback, useState } from "react";
import WheelIndicator from "wheel-indicator";
import { ScrollTrigger } from "gsap/ScrollTrigger";
import { gsap } from "gsap";
import { useGlobal, useDispatch } from "reactn"; // <-- reactn
import useTransitionDoneEvent from "hooks/useTransitionDoneEvent";
import { BrowserRouter as Router, Switch, matchPath, Route, useLocation, useRouteMatch } from "react-router-dom";

// --------------------------------------------------------------------- //
function ChapterNextSection(props) {
  const { nextSection, onGotoNext } = props;
  const [chapterNavEnabled, setChapterNavEnabled] = useGlobal("chapterNavEnabled");
  const isTransitionDone = useTransitionDoneEvent();
  const location = useLocation();

  const chapterNavEnabledRef = useRef(false);
  const ref = useRef();
  const progressRef = useRef();
  const transRef = useRef();
  const initialPathName = useRef(location.pathname);
  const locationChanged = useRef(false);

  const gotoNext = () => {
    // if the chapternav is expanded, triggering goto next
    // will cause serious issues, flickering stuttering etc
    if (chapterNavEnabledRef.current) return;

    // if the location has changed, due to clicking a link or anything,
    // prevent the ScrollTrigger gotoNext() call to cause a double trigger.
    if (locationChanged.current) return;

    if (!chapterNavEnabledRef.current) onGotoNext();
  };

  // if the location changes set a boolean to prevent the
  // onGotoNext in the scrolltrigger from triggering double
  useEffect(() => {
    if (location.pathname !== initialPathName.current) {
      locationChanged.current = true;
    }
  }, [location]);

  useEffect(() => {
    chapterNavEnabledRef.current = chapterNavEnabled;
  }, [chapterNavEnabled]);

  useEffect(() => {
    if (!isTransitionDone) return;
    let wheelIndicator;
    let triggerTime;
    let currentDrag;

    // -- scroll helpers
    let initWheelTrigger = () => {
      wheelIndicator = new WheelIndicator({
        elem: document.querySelector("body"),
        callback: () => {}
      });
    };

    let destroyWheelTrigger = () => {
      if (wheelIndicator) wheelIndicator.destroy();
    };

    let onWheelEvent = (data) => {
      onDirectionEvent(data.direction);
    };

    // ---- drag helpers
    let onDragEvent = (ev) => {
      var te = ev.changedTouches[0].clientY;
      if (!currentDrag) {
        currentDrag = te;
        return;
      }
      let direction = currentDrag > te ? "down" : "up";
      onDirectionEvent(direction);
    };

    let initDragTrigger = () => {
      window.addEventListener("touchmove", onDragEvent, false);
    };

    let destroyDragTrigger = () => {
      currentDrag = false;
      window.removeEventListener("touchmove", onDragEvent, false);
    };

    // -----

    let onDirectionEvent = (direction) => {
      if (direction === "up") return destroyWheelTrigger();
      if (direction !== "down") return;
      let elapsedTime = Date.now() - triggerTime;
      if (elapsedTime > 100) {
        gotoNext();
      }
    };

    let destroyTriggers = () => {
      destroyWheelTrigger();
      destroyDragTrigger();
    };
    let theTween;
    let theTweenTrigger;

    let initOtherTrigger = () => {
      if (!ref.current) return;

      let initialHeight = ref.current.offsetHeight;
      let height = window.innerHeight;
      ref.current.style.height = height + "px";
      let elProgress;
      // if (progressRef.current) {
      //   elProgress = progressRef.current.firstElementChild;
      // }

      // transRef
      setImmediate(() => {
        theTween = gsap.to(ref.current, {
          // y: 200,
          // y: "+=500px",
          yPercent: 40,
          ease: "none",
          scrollTrigger: {
            trigger: ref.current,
            start: `+=${initialHeight}px bottom`,
            end: `bottom bottom`,
            scrub: true,
            onEnter: () => {
              if (progressRef.current) {
                gsap.to(progressRef.current, { opacity: 1 });
              }
            },
            onUpdate: ({ progress }) => {
              if (transRef.current) {
                gsap.set(transRef.current, { opacity: 1 - progress });
              }
              // if (elProgress) {
              //   gsap.set(elProgress, { scaleX: progress });
              // }
            },
            onLeave: () => {
              gotoNext();
              // if (!locationChanged.current) {
              //   gotoNext();
              // }
            }
          }
        });

        theTweenTrigger = theTween.scrollTrigger;
      });

      // let otherTrigger = ScrollTrigger.create({
      //   trigger: ref.current,
      //   start: "bottom bottom",
      //   // markers: true,
      //   onEnter: () => {
      //     console.log("yeah");
      //     // triggerTime = Date.now();
      //     // if (window.matchMedia("(pointer:coarse)").matches) {
      //     //   initDragTrigger();
      //     // } else {
      //     //   initWheelTrigger();
      //     // }
      //   }
      // });
    };

    let trigger = ScrollTrigger.create({
      trigger: ref.current,
      start: "center bottom",
      end: "bottom bottom",
      // markers: true,

      onEnter: (e) => {
        console.log(e);
        setTimeout(() => {
          initOtherTrigger();
        }, 500);
        // triggerTime = Date.now();
        // if (window.matchMedia("(pointer:coarse)").matches) {
        //   initDragTrigger();
        // } else {
        //   initWheelTrigger();
        // }
      },
      onEnterBack: () => {
        if (ref.current) {
          ref.current.style.height = "auto";
        }

        // console.log("enter back");
        // destroyTriggers();
      }
    });

    return () => {
      if (trigger) trigger.kill(true);
      if (theTween) theTween.kill(true);
      if (theTweenTrigger) theTweenTrigger.kill(true);

      destroyTriggers();
    };
  }, [isTransitionDone]);
  return (
    <div className="chapter-page__next" onClick={gotoNext} ref={ref}>
      <div ref={transRef}>
        <span>Keep scrolling for</span>
        <h3>{nextSection.title}</h3>
      </div>
      {/* <div className="progress" ref={progressRef}>
        <span></span>
      </div> */}
    </div>
  );
}

export default ChapterNextSection;
