import { useGlobal, useDispatch } from "reactn"; // <-- reactn
import { gsap } from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";
import { useParams, useRouteMatch, useLocation, useHistory } from "react-router-dom";
import { SwitchTransition, TransitionGroup, Transition } from "react-transition-group";
import eventBus from "common/EventBus";
import { Provider as BusProvider, useBus, useListener } from "react-bus";

import ChapterPagesContinous from "./ChapterPagesContinous";
import ChapterPagesSeperate from "./ChapterPagesSeperate";
gsap.registerPlugin(ScrollTrigger);

