import { setGlobal, useGlobal, useDispatch } from "reactn";
import { gsap } from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";
import { useImperativeHandle, useRef, useEffect, useState, forwardRef } from "react";
import BookChapterLogic from "./BookChapterLogic";
import { disableBodyScroll, enableBodyScroll, clearAllBodyScrollLocks } from "body-scroll-lock";
import IconNumber from "./IconNumber";
import {
  BrowserRouter as Router,
  Switch,
  Route,
  NavLink,
  useParams,
  useRouteMatch,
  useHistory
} from "react-router-dom";
gsap.registerPlugin(ScrollTrigger);

// -- BookChapterNav ---------------------------------------------------- //
const BookChaptersUI = forwardRef((props, ref) => {
  const {
    fullCollapse,
    isDynamic,
    debug,
    noMouse,
    startCollapsed,
    defaultExpanded,
    isVisible,
    unFocusChapterNav,
    onRowClickCB,
    onActivateRow,
    onNavDisabled,
    onNavEnabled,
    onHomeClick,
    isExpanded,
    isEnabled,
    test
  } = props;

  const activateBlur = useDispatch("blur");

  const [chaptersData] = useGlobal("chaptersData");
  const [currentChapter] = useGlobal("currentChapter");
  const itemEls = useRef([]);
  let history = useHistory();

  const [globalUnfocusChapterNav, setGlobalUnfocusChapterNav] = useGlobal("unFocusChapterNav");

  const otherRef = useRef();
  const helper = useRef(); // ref is equivalent of instance prop

  let onRowClick = (url) => {
    if (!helper.current) return false;

    if (isDynamic) {
      helper.current.disable(true);
    }
    if (onRowClickCB) {
      onRowClickCB();
    }
    setImmediate(() => {
      history.push(url);
    });
  };

  useEffect(() => {
    if (!helper.current) {
      helper.current = new BookChapterLogic({
        el: otherRef.current,
        rows: itemEls.current,
        isDynamic: isDynamic,
        expanded: !startCollapsed,
        noMouse: noMouse,
        onNavEnabled: onNavEnabled,
        onNavDisabled: onNavDisabled,
        onRowClick: onRowClick,
        onActivateRow: onActivateRow,
        isExpanded: isExpanded,
        isEnabled: isEnabled
      });
      helper.current.init();
    }
  }, []);

  useImperativeHandle(ref, () => {
    return {
      control: helper
    };
  });

  return (
    <nav tabIndex="0" className={`book-chapters ${debug ? "is-debug" : ""}`} ref={otherRef}>
      <div className="book-chapters__hitarea"></div>
      {/* <div className="book-chapters__trigger"></div> */}
      {/* <NavLink to={`/`} className="book-chapters__home">
        <span>Home</span>
      </NavLink> */}
      <ul>
        <li
          className="book-chapters__item chapter-row chapter-row--home "
          ref={(element) => (itemEls.current[0] = { slug: "home", el: element })}
        >
          <NavLink exact to={`/`} onClick={onHomeClick}>
            <div className="chapter-row__inner  ">
              <span className="chapter-row__nr js-nr"></span>
              <span className="chapter-row__title js-title">Home</span>
            </div>
            <div className="chapter-row__hover js-hover-area"></div>
          </NavLink>
        </li>
        {chaptersData.map((chapter, key) => (
          <li
            key={key}
            className="book-chapters__item chapter-row "
            ref={(element) => (itemEls.current[key + 1] = { slug: chapter.slug, el: element })}
          >
            <NavLink to={`/${chapter.slug}`}>
              {/* <a href={`/${chapter.slug}`} tabIndex="0"> */}
              {/* <NavLink to={`/${chapter.slug}`}> */}
              <div className="chapter-row__inner  ">
                <div className="chapter-row__line js-line"></div>
                <span className="chapter-row__nr js-nr">
                  <IconNumber nr={chapter.chapter_nr}></IconNumber>
                </span>
                <span className="chapter-row__title js-title">{chapter.title}</span>
              </div>
              <div className="chapter-row__hover js-hover-area"></div>
              {/* </NavLink> */}
              {/* </a> */}
            </NavLink>
          </li>
        ))}
      </ul>
    </nav>
  );
});

export default BookChaptersUI;
