function InlineFoldout(props) {
  const { name, position, text } = props;
  const [expanded, setExpanded] = useState(false);
  const [clamped, setClamped] = useState(false);

  const refContainer = useRef();
  const refPanel = useRef();
  const refSizer = useRef();
  const refPanelInner = useRef();
  const collapsedSize = useRef;
  const fullDimension = useRef();
  const prevState = useRef();
  const [theText, setTheText] = useState(props.text);

  let onClick = () => {
    setExpanded(!expanded);
  };

  useEffect(() => {
    fullDimension.current = {
      width: refPanelInner.current.offsetWidth,
      height: refPanelInner.current.offsetHeight
    };

    gsap.set(refPanelInner.current, {
      height: refSizer.current.offsetHeight,
      width: refSizer.current.offsetWidth
    });

    // gsap.set(refPanelInner.current, { height: "100px", width: "100px" });
  }, []);

  useEffect(() => {
    if (!refContainer.current) return;
    let tween;

    setTimeout(() => {
      setClamped(true);

      if (window.matchMedia("(min-width:600px)").matches) {
        tween = gsap.to(refContainer.current, {
          y: -80,
          ease: "none",
          scrollTrigger: {
            trigger: refContainer.current,
            start: "top bottom",
            end: "bottom top",
            scrub: true
          }
        });
      }
    }, 500);

    return () => {
      if (tween) tween.kill(true);
    };
  }, [refContainer]);

  useEffect(() => {
    if (!expanded && !prevState.current) return;

    if (expanded) {
      setClamped(false);

      gsap.to(refPanelInner.current, {
        height: fullDimension.current.height,
        width: fullDimension.current.width,
        duration: 0.3,
        ease: "Power4.easeOut",
        onComplete: () => {
          ScrollTrigger.refresh();
          prevState.current = true;
        }
      });

      refContainer.current.focus();
    } else {
      gsap.to(refPanelInner.current, {
        height: refSizer.current.offsetHeight,
        width: refSizer.current.offsetWidth,
        duration: 0.3,
        ease: "Power4.easeOut",
        onComplete: () => {
          setClamped(true);
          prevState.current = false;
          ScrollTrigger.refresh();
        }
      });
    }
  }, [expanded]);

  return (
    <ParallaxLayer initDelay={750} offsetY={-80} minScreenWidth={600}>
      <div
        className={`inline-foldout ${expanded ? "is-expanded" : ""} ${clamped ? "is-clamped" : ""}`}
        onClick={onClick}
        role="dialog"
        ref={refContainer}
        aria-expanded={expanded}
      >
        <div className="inline-foldout__meta">
          <span className="meta__name">{name}</span>
          <span className="meta__position">{position}</span>
        </div>
        <div className="inline-foldout__sizer" ref={refSizer}></div>
        <div className="inline-foldout__panel foldout-panel" ref={refPanel}>
          <div className="foldout-panel__inner" ref={refPanelInner}>
            <div className="foldout-panel__wrapper" dangerouslySetInnerHTML={{ __html: theText }}></div>
          </div>
          <div className="foldout-panel__footer">
            <button>{`${expanded ? "Close" : "Read more"}`}</button>
          </div>
        </div>
      </div>
    </ParallaxLayer>
  );
}

function ParallaxImage(props) {
  const { image } = props;

  const imageRef = useRef(null);
  const [lightboxImage, setLightboxImage] = useState(false);
  const [appBlur, setAppBlur] = useGlobal("appBlur");
  const [imageBnd, setImageBnd] = useState(false);

  // define the responsive image sizes
  var srcSetSizes = "(min-width: 1024px) 10vw, (min-width: 600px) 15vw, 35vw";

  // var srcSetSizes = "5vw";

  useEffect(() => {
    gsap.set(imageRef.current, { left: `${40 * Math.random()}%`, yPercent: 0 });

    let tween = gsap.to(imageRef.current, {
      yPercent: -50,
      ease: "none",
      scrollTrigger: {
        trigger: imageRef.current,
        start: "top bottom",
        end: "bottom top",
        scrub: true
      }
    });

    return () => {
      if (tween) tween.kill(true);
    };
  }, []);

  let onClick = () => {
    setImageBnd(imageRef.current.getBoundingClientRect());
    setLightboxImage(true);
  };

  let onClose = () => {
    setLightboxImage(false);
  };

  let getImage = () => {
    return imageRef.current;
  };
  return (
    <>
      <div onClick={onClick}>
        <Image
          ref={imageRef}
          keepAspect="true"
          {...image}
          sizes={srcSetSizes}
          onClick={onClick}
          caption={false}
        ></Image>
      </div>
      <OverlayLightbox
        onClose={onClose}
        visibleToggle={lightboxImage}
        clickedImageRef={imageRef}
        image={image}
        getImage={getImage}
      ></OverlayLightbox>
    </>
  );
}

function OverlayLightbox(props) {
  const { visibleToggle, onClose, image, clickedImageRef } = props;
  const [visible, setVisible] = useState(props.visibleToggle);
  const [scale, setScale] = useState(1);
  const transImageRef = useRef(null);
  const imageRef = useRef(null);
  const ref = useRef(null);

  let options = {
    minScale: 0,
    centerContent: true,
    limitToBounds: false
  };

  let wheelOptions = {
    disabled: true
  };
  let defaultScale = 1;

  let transImageStyle = {
    position: "absolute"
  };

  let calcEnlargedScale = (imgWidth, imgHeight) => {
    let vPadding = 140;
    // let hPadding = 40;

    if (window.matchMedia("(min-width:600px)").matches) {
      vPadding = 60;
    }

    //
    let wHeight = window.innerHeight * 0.9 - vPadding;
    let wWidth = window.innerWidth * 0.9;

    let startDimensions = { w: imgWidth, h: imgHeight };
    let adjustedOptions = {};
    let adjustedScale;

    let scale = 1;

    let imageAspect = imgHeight / imgWidth;
    let viewerAspect = wHeight / wWidth;

    //
    if (imgHeight < wHeight && imgWidth < wWidth) {
      scale = 1;
    } else {
      if (imageAspect < viewerAspect) {
        scale = wWidth / imgWidth;
      } else {
        scale = wHeight / imgHeight;
      }
    }

    return scale;
  };

  const enlargedScale = calcEnlargedScale(image.width, image.height);
  const srcSize = `${Math.round(((image.width * enlargedScale) / window.innerWidth) * 100)}vw`;

  let onEnter = () => {
    // console.log("onEter");
    gsap.set(transImageRef.current, { opacity: 0, position: "absolute", width: image.width, height: image.height });
    gsap.set(imageRef.current, { opacity: 0, width: image.width, height: image.height });
    setTimeout(() => {
      gsap.set(clickedImageRef.current, { opacity: 0 });

      let imageBnd = clickedImageRef.current.getBoundingClientRect();
      let targetBnd = transImageRef.current.getBoundingClientRect();

      let toScale = calcEnlargedScale(image.width, image.height);

      let start = {
        x: imageBnd.x + "px",
        y: imageBnd.y + "px",
        opacity: 1,
        scale: imageBnd.width / image.width
      };

      // // position the scalable image
      gsap.set(imageRef.current, {
        x: window.innerWidth / 2 - image.width / 2 + "px",
        y: window.innerHeight / 2 - image.height / 2 + "px",
        transformOrigin: "center center",

        scale: toScale
      });

      // // // do the transition
      // // // initial
      gsap.set(transImageRef.current, {
        ...start,
        transformOrigin: "top left"
      });

      // // //
      gsap.to(transImageRef.current, {
        x: window.innerWidth / 2 - (image.width * toScale) / 2 + "px",
        y: window.innerHeight / 2 - (image.height * toScale) / 2 + "px",
        scale: toScale,
        duration: 0.2,
        onComplete: () => {
          gsap.set(imageRef.current, { opacity: 1 });
          gsap.set(transImageRef.current, { opacity: 0 });
        }
      });
      return;
    }, 200);
  };

  let onExit = () => {
    let imageBnd = clickedImageRef.current.getBoundingClientRect();

    gsap.set(transImageRef.current, { opacity: 1 });
    gsap.set(imageRef.current, { opacity: 0 });

    gsap.to(transImageRef.current, {
      x: imageBnd.x + "px",
      y: imageBnd.y + "px",
      scale: imageBnd.width / image.width,
      duration: 0.2,
      onComplete: () => {
        gsap.set(clickedImageRef.current, { opacity: 1 });
        gsap.set(transImageRef.current, { opacity: 0 });
      }
    });
  };

  useEffect(() => {
    setVisible(visibleToggle);
  }, [visibleToggle]);

  // prepare image size

  // lock scrolling when element appears
  useEffect(() => {
    if (visible) {
      disableBodyScroll(ref.current);
    } else {
      clearAllBodyScrollLocks();
    }

    return () => {
      clearAllBodyScrollLocks();
    };
  }, [visible]);

  useKeypress("Escape", onClose);
  return (
    <Transition in={visible} onEnter={onEnter} onExit={onExit} timeout={500} mountOnEnter unmountOnExit>
      <div className="overlay-lightbox" onClick={onClose} ref={ref}>
        <div>
          <img
            src={image.src}
            srcSet={image.srcset}
            sizes={srcSize}
            alt={image.alt}
            ref={transImageRef}
            className="transition-image"
            aria-hidden="true"
            // sizes="100vw"
          ></img>
          <img
            src={image.src}
            srcSet={image.srcset}
            alt={image.alt}
            ref={imageRef}
            sizes={srcSize}

            // style={transImageStyle}
          />
        </div>
      </div>
    </Transition>
  );
}
function DoubleColumnSection(props) {
  const { text, image, foldout } = props;

  return (
    <div className="double-column">
      <div className="double-column__text">
        {image && (
          <div className="image">
            <ParallaxImage image={image}></ParallaxImage>
          </div>
        )}
        <div className="text" dangerouslySetInnerHTML={{ __html: text }}></div>
      </div>
      <div className="double-column__quote">
        {foldout && <InlineFoldout name={foldout.name} position={foldout.position} text={foldout.text}></InlineFoldout>}
      </div>
    </div>
  );
}

function ElementDoubleColumn(props) {
  // console.log(props);
  const { content } = props;
  return (
    <div className="component component--double-column container">
      {content.map((content, index) => (
        <DoubleColumnSection
          key={index}
          foldout={content.foldout}
          text={content.text}
          image={content.image}
        ></DoubleColumnSection>
      ))}
    </div>
  );
}

export default ElementDoubleColumn;
